// import { IconButton } from "@mui/material";
import { Image, VideoCamera, YoutubeLogo } from "phosphor-react";
import TextareaAutosize from "react-textarea-autosize";
import React, { useContext ,useState,useEffect} from "react";
import { uidContext } from "../AppContext";
import image1  from "../../images/feed-1.jpg"
import {dateParser} from "../Utils"
import axios from "axios";
import { useDispatch } from "react-redux";
import { fetchAllPostsAction } from "../../redux/slices/Posts";
import { SERVER_URL } from "../../Domain";
import Resizer from "react-image-file-resizer";

import { Avatar } from "@mui/material";


import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import MuiAlert from '@mui/material/Alert';
import CardText from "../Posts/CardText";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});


const Craetepost = ({ user,socket }) => {


  const uid = useContext(uidContext);
  const [isLoading,setIsLoading] = useState(true)
  const [message,setMessage] = useState("")
  const [postPicture,setPostpicture] = useState(null)
  const [video,setVideo] = useState("")
  const [file,SetFile] = useState("")
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();
  const [uploadProgress, setUploadProgress] = useState(0);
  const [colorsChoosed,setcolorsChoosed] = useState()
  const defaultColor = "radial-gradient(circle, #5c0067 0%, #00d4ff 100%)";
  const [color,setColor] = useState()
  const [createPost,setcreatePost] = useState(false);

const resizeFile = (file) =>{
  new Promise((resolve) => {
    Resizer.imageFileResizer(
      file,
      300,
      300,
      "JPEG",
      100,
      0,
      (uri) => {
        resolve(uri);
      },
      "file"
    );
  })
};

const handlePicture = async(e)=>{
  if(e){
  setPostpicture(URL.createObjectURL(e.target.files[0]))
    try {
    const file = e.target.files[0];
    const image = await resizeFile(file);
    console.log(image);
  } catch (err) {
    console.log(err);
  }
  }
  setVideo("")
  SetFile(e.target.files[0])
}

useEffect(() => {
const handleVideo = ()=>{
let findLink = message.split(" ")
for(var i = 0 ; i< findLink.length;i++){
  if(findLink[i].includes("https://www.youtube.com") || findLink[i].includes("https://youtube.com")){
   let embed = findLink[i].replace("watch?v=","embed/")
   setVideo(embed.split("&")[0])
   findLink.splice(i,1)
   setMessage(findLink.join(" "))
   setPostpicture("")
  }else{
     if(findLink[i].includes("https://youtu.be/")){
    let embed = findLink[i].replace("youtu.be/","www.youtube.com/embed/")
    setVideo(embed)
    findLink.splice(i,1)
    setMessage(findLink.join(" "))
   setPostpicture("")
  } 
  }

}
}

  
   if(uid){
    setIsLoading(false)
     }
handleVideo()
  }, [uid,message,video])



   const cancelPost=()=>{
    setMessage("")
    setVideo("")
    setPostpicture("")
   }
    const deleteVideo=()=>{
    setVideo("")
   }
       const deletePicture=()=>{
    setPostpicture("")
   }

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const action = (
    <>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        
      </IconButton>
    </>
  );





   const handlePost = async(e)=>{
    e.preventDefault()
    if(message || file || video){
      const data = new FormData()
      data.append("file",file)
      data.append("posterId",uid)
      data.append("message",message)
      data.append("video",video)
      data.append("color",color ? color : defaultColor)
        await axios({
      method: "post",
      url: `${SERVER_URL}/api/post/`,
      withCredentials: true,
      data,
      onUploadProgress:(progressEvent)=>{
        const progress = Math.round((progressEvent.loaded / progressEvent.total) * 100);
        setUploadProgress(progress)
      }
    })
    .then(({data})=>{
      cancelPost()
      const fetchData = async () => {
        await axios({
          method: "GET",
          url: SERVER_URL+"/api/post/",
          withCredentials: true,
        })
          .then(({ data }) => {
            const Array = data.slice(0, 5);
            dispatch(fetchAllPostsAction.fetchAllPosts(Array));
            console.log("fetch again from create post")
            socket.emit("NewPost",{
               data
            })
            //send notification to my followers peoples in database
            if(user.followers.length !==0){

          for(var i=0;i< user.followers.length;i++){

             axios({
    method: "patch",
    url: `${SERVER_URL}/api/post/notification/${user.followers[i]}`,
    data :{
          notifieId:user._id,
          notifiePseudo: user.pseudo,
          notification: "post a new post",
          status:"pending",
          pictureNotifie:user.picture
    },
    withCredentials : true
   }).then(({data})=>{
    // send real time notifications to everyone who follow me
    socket.emit("sendNotification",{
          senderName:user._id,
          resverName:data._id, 
          timeNot: new Date()
        })
   }).catch((err)=>console.log(err))

          }
            }



          })
          .catch((err) => {
            console.log(err);
          });
      };
      fetchData();
    }).catch((err)=>console.log(err))


    }else{
    setOpen(true)
    }
    
   }
function ProgressBar({ progress }) {
  return (
    <div className="progress-bar">
      <div className="progress" style={{ width: `${progress}%`,backgroundColor:"red" }}>{progress} %</div>
    </div>
  );
}
function generateRandomColors() {
  var colors =[] ;
  for (var i = 0; i < 50; i++) {
    var red = Math.floor(Math.random() * 256);
    var green = Math.floor(Math.random() * 256);
    var blue = Math.floor(Math.random() * 256);
    var colorPallete = ["yellow","white","gray","gold","blue","silver","turquoise","cyan","brown","pink","purple","green"]
    var lastcolor = Math.floor(Math.random() * colorPallete.length);
  var color = "rgb(" + red + "," + green + "," + blue + ")";
  var backgroundImage = "linear-gradient(to bottom right, " + color + ", " + colorPallete[lastcolor] +")";
  colors.push(backgroundImage)

  }
  return setcolorsChoosed(colors)
}
useEffect(()=>{
  generateRandomColors()
},[])

useEffect(()=>{
var span = document.querySelectorAll(".colorPallete span")
span.forEach((item)=>{
item.addEventListener("click",(e)=>{
  setColor(e.target.id)
})
})
})

  return (
    <div>
      {
        isLoading ?
         (<h5>loading..........</h5>) :
      (
        <form onSubmit={(e)=>handlePost(e)} >
          <div className="create-post">
            <div className="profile-picturem">
              <Avatar src={user.picture} alt="asa" />
            </div>
            <TextareaAutosize
              id="create-post"
              placeholder="whats something new "
              onChange={(e)=>setMessage(e.target.value)}
              value={message}
              />
            <input type="submit"  value="Post" className="btn btn-primary" />
          </div>

          <div className="vidoe_yt_img">
            <i>
              <IconButton>
                <VideoCamera onClick={()=>setcreatePost(true)}/>
              </IconButton>
            </i>
            <span>video</span>
            <i  className="fileSelecter">
              <IconButton>
                <Image onClick={()=>setcreatePost(true)} />
              </IconButton>
            </i>
            <span>picture</span>
            <i>
              <IconButton>
                <YoutubeLogo onClick={()=>setcreatePost(true)} />
              </IconButton>
            </i>
            <span>youtube</span>
          </div>
            <ProgressBar progress={uploadProgress} />

        </form>
        )
    }
    {
    (createPost) && 
      <>
        <div className="popupCreatePost">
          <div className="cancelPost" onClick={()=>setcreatePost(false)}>
            
          < i className="ri-close-fill"></i>

          </div>
          <div className="mainPost">
          <div className="middleMain">
            <div className="postLookLike"> 
            <p>Previsualization</p>
            {
              (!message && !postPicture && !video) && 
            <div className="Previsualization">once your start 
            creating post here will show your 
            how post will look
            </div>
            }

            <>
        {
          (message || postPicture || video) && 
           <>
           <div className="feeds">
            <div className="feed">
              <div className="head">
                <div className="user">
                  <div className="profile-picture">
                    <Avatar src={user.picture} alt={user.pseudo}/>
                  </div>
                  <div className="ingo">
                    <span><h4>{user.pseudo}</h4></span>
                    <small>{dateParser(Date.now())}</small>
                  </div>
                </div>
                <span className="edit "> 
                </span>
              </div>
              {!video && !postPicture && <div className="news ">
              <CardText type="posts" text={message} color={color}/>
              </div>}
              {(video || postPicture) && <div className="news "><b>{message}</b></div>}
              {video &&
                  <div className="photo ">
                  <iframe height="250" width="400" src={video} title={Math.random()}></iframe>
                  </div>
               }
               {postPicture &&
                  <div className="photo ">
                  <img src={postPicture} alt="abc"/>
                  </div>
               }
               
            </div>
          </div>
          </>
        }

        <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        action={action}
      >
      <Alert onClose={handleClose} severity="warning" sx={{ width: '100%' }}>
        veuillez entrer un message pour poster
        </Alert>
      </Snackbar>
    </>
            </div>
            <div className="outils">
              <p>Creating post</p>
               {
                (!message && !postPicture && !video) &&
               <>
              <div className="choose">
              <svg aria-label="Ic√¥ne pour repr√©senter le contenu multim√©dia, comme les images ou les vid√©os" class="x1lliihq x1n2onr6" color="var(--color-name)" fill="rgb(245, 245, 245)" height="100" role="img" viewBox="0 0 97.6 77.3" width="100">
              <title>Ic√¥ne pour repr√©senter le contenu multim√©dia, comme les images ou les vid√©os</title>
              <path d="M16.3 24h.3c2.8-.2 4.9-2.6 4.8-5.4-.2-2.8-2.6-4.9-5.4-4.8s-4.9 2.6-4.8 5.4c.1 2.7 2.4 4.8 5.1 4.8zm-2.4-7.2c.5-.6 1.3-1 2.1-1h.2c1.7 0 3.1 1.4 3.1 3.1 0 1.7-1.4 3.1-3.1 3.1-1.7 0-3.1-1.4-3.1-3.1 0-.8.3-1.5.8-2.1z" fill="currentColor"></path>
              <path d="M84.7 18.4 58 16.9l-.2-3c-.3-5.7-5.2-10.1-11-9.8L12.9 6c-5.7.3-10.1 5.3-9.8 11L5 51v.8c.7 5.2 5.1 9.1 10.3 9.1h.6l21.7-1.2v.6c-.3 5.7 4 10.7 9.8 11l34 2h.6c5.5 0 10.1-4.3 10.4-9.8l2-34c.4-5.8-4-10.7-9.7-11.1zM7.2 10.8C8.7 9.1 10.8 8.1 13 8l34-1.9c4.6-.3 8.6 3.3 8.9 7.9l.2 2.8-5.3-.3c-5.7-.3-10.7 4-11 9.8l-.6 9.5-9.5 10.7c-.2.3-.6.4-1 .5-.4 0-.7-.1-1-.4l-7.8-7c-1.4-1.3-3.5-1.1-4.8.3L7 49 5.2 17c-.2-2.3.6-4.5 2-6.2zm8.7 48c-4.3.2-8.1-2.8-8.8-7.1l9.4-10.5c.2-.3.6-.4 1-.5.4 0 .7.1 1 .4l7.8 7c.7.6 1.6.9 2.5.9.9 0 1.7-.5 2.3-1.1l7.8-8.8-1.1 18.6-21.9 1.1zm76.5-29.5-2 34c-.3 4.6-4.3 8.2-8.9 7.9l-34-2c-4.6-.3-8.2-4.3-7.9-8.9l2-34c.3-4.4 3.9-7.9 8.4-7.9h.5l34 2c4.7.3 8.2 4.3 7.9 8.9z" fill="currentColor"></path>
              <path d="M78.2 41.6 61.3 30.5c-2.1-1.4-4.9-.8-6.2 1.3-.4.7-.7 1.4-.7 2.2l-1.2 20.1c-.1 2.5 1.7 4.6 4.2 4.8h.3c.7 0 1.4-.2 2-.5l18-9c2.2-1.1 3.1-3.8 2-6-.4-.7-.9-1.3-1.5-1.8zm-1.4 6-18 9c-.4.2-.8.3-1.3.3-.4 0-.9-.2-1.2-.4-.7-.5-1.2-1.3-1.1-2.2l1.2-20.1c.1-.9.6-1.7 1.4-2.1.8-.4 1.7-.3 2.5.1L77 43.3c1.2.8 1.5 2.3.7 3.4-.2.4-.5.7-.9.9z" fill="currentColor"></path>
              </svg>
              </div>
              <div className="filechoose" onClick={()=>setMessage("üëáüëáüëáclick to the icon bellow to choose a picture")}> Choose file in your device</div>
              <div className="textchoose" onClick={()=>setMessage("start writting whats in your mind")}> Create text post</div>
              </>  
              }
              {
                (message || postPicture || video) &&
              <div className="inputpost">
              <TextareaAutosize placeholder="something new"
              onChange={(e)=>setMessage(e.target.value)}
              value={message}
              />
              <div className="ri-fill-media">
            <i  className="fileSelecter">
              <div>
                <i className="ri ri-image-fill"></i>
                <input type="file" className="inputPicture" name="file" onChange={(e)=>handlePicture(e)} accept=".jpg,.jpeg,.png" title="choose your file "/>
              </div>
            </i>
              </div>
              </div>                
              }


              {!video && !postPicture && message && <div className="news ">
              <div className="colorPallete">
              {
                colorsChoosed && 
                colorsChoosed.map((color,index)=>(
                 <span 
                 key={index} id={color} style={{backgroundImage:color}}
                 ></span>
                  ))
              }
              <span><input type="color"/></span>
              </div>
              </div>}
          { postPicture || message || video.length > 20  ? <button className="btn-new" onClick={cancelPost}>annuler</button> : null}
          {video ? <button className="btn-new" onClick={deleteVideo}>delete video</button> : null}
          {postPicture ? <button className="btn-new" onClick={deletePicture}>delete picture</button> : null}
            </div>
          </div>
            <div className="partager">
            <button className="btn btn-primary" onClick={handlePost}>partager</button>
            
            </div>
          </div>
        </div>
        </>
      }
    </div>
  );
};

export default Craetepost;
